.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
}

.dialog {
    color: white;
    text-decoration: none;
}

.dialog img {
    width: 40px;
    border-radius: 30px;
    padding-bottom: 7px;
}

.dialog a {
    text-decoration: none;
    color: #ff0888;
    padding: 7px;
    font-weight: bold;
    font-size: 18px;
}

.dialog a:focus {
    color: #ffe600;
}

.messages {
    position: relative;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    min-width: 0;
    background-color: #fff;
    background-clip: border-box;
    border: 0 solid rgba(0,0,0,.125);
    box-shadow: 0 0 1.25rem 0 rgb(0 0 0 / 10%);
    border-radius: 15px;
    width: 100%;
    padding-bottom: 25px;
}

.messagesBlock {
    margin-top: 15px;

}

.message{
    margin: 0 15px;
}

.messagesInput textarea{
    margin: 15px 15px 0;
    width: 96%;
    height: 40px;
}

.buttonOnSend{
    float: right;
    margin: 5px 15px 0 0;
    outline: 0!important;
    border: 0;
    background: none;
}